<template>
	<div>
		<TambahSprint />
		<v-card>
			<v-card-title class="py-2">Sprint</v-card-title>
			<v-card-text>
				<!-- <Loader v-if="isLoading" /> -->
				<v-data-table
					class="mt-0"
					:headers="header"
					:items="data"
					:items-per-page="10">	
					<template v-slot:item.no="{item}">
						{{data.map(x => x.id).indexOf(item.id)+1}}
					</template>
					<template v-slot:item.progress="{}">
						<v-btn
							depressed
							color="cyan"
							@click="$router.push({ name: 'progress-tenant' })"
							dark small>
							manage
						</v-btn>
					</template>
					<template v-slot:no-data>
						<v-alert type="info" class="mt-4">
							<div>Data tidak ditemukan</div>
						</v-alert>
					</template>
					<template v-slot:item.action="{}">
						<v-btn
							icon
							color="primary">
							<v-icon small>mdi-pencil</v-icon>
						</v-btn>
						<v-btn
							icon
							color="error">
							<v-icon small>mdi-trash-can</v-icon>
						</v-btn>
					</template>
				</v-data-table>
			</v-card-text>
		</v-card>
	</div>
</template>

<script>
	// import { mapState } from 'vuex'
	// import Loader from '@/components/Loader'
  import TambahSprint from './TambahSprint'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Sprint', value: 'sprint', sortable: false },
				{ text: 'Deadline', value: 'deadline', sortable: false },
				{ text: 'Actions', value: 'action', sortable: false },
				{ text: 'Progress', value: 'progress', sortable: false },
			],
			data: [{
				id: 1,
				sprint: 'Minggu ke 1',
				deadline: '2020/11/20'
			}]
		}),

		components: {
			// Loader
			TambahSprint
		},		

		// computed: {
		// 	...mapState('dataIncubatorTraction', ['dataTraction', 'isLoading'])
		// }
	}
</script>