<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn
        v-on="on"
        depressed
        color="primary"
        class="mb-6">
        create sprint
        <v-icon class="ml-3">mdi-plus</v-icon>
      </v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Tambah Sprint</span>
      </div>
      <v-form v-model="valid" ref="form">
        <v-card-text>
          <v-text-field
            v-model="dataSprint.nama"
            label="Name"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field> 
          <v-dialog
              ref="dialog"
              v-model="modal"
              :return-value.sync="dataSprint.tgl"
              persistent
              width="290px">
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="dataSprint.tgl"
                  label="Deadline"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-on="on"
                  :rules="requiredRule"
                ></v-text-field>
              </template>
              <v-date-picker v-model="dataSprint.tgl" scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.dialog.save(dataSprint.tgl)">OK</v-btn>
              </v-date-picker>
            </v-dialog>  

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  export default {
    data () {
      return {
        dialog: false,
        modal: false,
        valid: true,
        dataSprint: {
          nama: '',
          tgl: ''
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: {
      Notif
    },

    computed: {
      ...mapState('dataIncubatorMentor', ['dataMentor', 'isLoading', 'status']),      
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          // this.$store.dispatch('dataIncubatorMentor/postDataMentor', data)
          // this.$store.dispatch('dataIncubatorMentor/updateIsLoading', true)
          // this.msg.visible = true          
        }
      },
      visible(val) {
        this.msg.visible = val
        // this.$store.commit('dataIncubatorMentor/updateStatus', null)
      }
    }
  }
</script>